.container{
  margin-top:0px;
}

@media only screen and (max-width: 700px) {
  .courtRight { 
    display:none;
  }
  .courtLeft {
    display:none;
  }
  .karlBBQ{
    display: none;
  }
  .karlSmirk{
    display: none;
  }
  .oldWolves{
    display: none;
  }
  .newWolves{
    display: none;
  }
}

.courtRight { 
  max-width: width 30%;
  min-height: 908px;
  background-image: url(/static/media/Court_Right.c2995628.png);
  background-position:right;
  background-repeat: no-repeat;
  background-size: cover;
}

.courtRight::after {
  max-width: width 30%;
  min-height: 908px;
  display: block;
  position: relative;
  background-image: -webkit-gradient(linear, right top, left top, color-stop(0, rgba(255, 255, 255, 0)), to(#fff));
  background-image: -webkit-linear-gradient(right, rgba(255, 255, 255, 0) 0, #fff 100%);
  background-image: linear-gradient(to left, rgba(255, 255, 255, 0) 0, #fff 100%);
  height: 150px;
  width: 100%;
  content: '';
}

.courtLeft {
  max-width: width 30%;
  min-height: 908px;
  background-image: url(/static/media/Court_Left.92d4df74.png);
  background-position:left;
  background-repeat: no-repeat;
  background-size: cover;
}

.courtLeft::after {
  max-width: width 30%;
  min-height: 908px;
  display: block;
  position: relative;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(0, rgba(255, 255, 255, 0)), to(#fff));
  background-image: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0, #fff 100%);
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0) 0, #fff 100%);
  height: 150px;
  width: 100%;
  content: '';
}

