body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.tableContainer {
    padding-top:1px;
display: flex;
align-items: center;
justify-content: center;
width: width 30%;
}

.red{
    color:#EF3636;
}

.green{
    color:green;
}

.tableCell{
    white-space: nowrap;
    max-width: 50vw;
}

.simButton{
    padding-top: 14px;
    padding-bottom: 10px;
}

.counter{
font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
justify-content: space-evenly;
}
.tableData{
    padding-left: 10px;
    padding-right: 10px;
}
.zionImage{
background-image: url(/static/media/zionWolves.97d3bd33.png);
background-size: contain;
background-repeat: no-repeat;
background-position: top;
padding-top: 10px;
}

.morantImage{
background-image: url(/static/media/ja_morant.a8041d05.jpg);
background-size: contain;
background-repeat: no-repeat;
background-position: top;
}

.head {
    padding-top:10px;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#dd9b4f), to(white));
    background-image: -webkit-linear-gradient(#dd9b4f, white);
    background-image: linear-gradient(#dd9b4f, white);
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    text-align: center;
    padding-bottom: 5px;
    margin-bottom: 0%;
    height:100px;
}

.wolves{
    padding-top:10px;
    background-image: -webkit-gradient(linear, right top, left top, from(#0c2340),color-stop(#236192), to(#78be20));
    background-image: -webkit-linear-gradient(right, #0c2340,#236192, #78be20);
    background-image: linear-gradient(-90deg, #0c2340,#236192, #78be20);
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    color:white;
    text-align: center;
    padding-bottom: 5px;
    margin-bottom: 0%;

}

